<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="row mt-4">
            <div class="col-md-4 mb-4" v-for="(item, key) in products" :key="key">
                <div class="card border-0 shadow-sm">
                    <div style="height: 150px; background-size: cover; background-position: center" :style="{backgroundImage: `url(${item.imageUrl})`}"></div>
                    <div class="card-body">
                        <span class="badge badge-secondary float-right ml-2"> {{item.category}}</span>
                        <h5 class="card-title">
                            <a href="#" class="text-dark">{{item.title}}</a>
                        </h5>
                        <p class="card-text">{{item.contemt}}</p>
                        <div class="d-flex justify-content-between align-items-baseline">
                            <div class="h5" v-if="!item.price">{{item.origin_price}} 元</div>
                            <del class="h6" v-if="item.price">原價 {{item.origin_price}} 元</del>
                            <div class="h5" v-if="item.price">現在只要 {{item.price}} 元</div>
                        </div>
                    </div>
                    <div class="card-footer d-flex">
                        <button type="button" class="btn btn-outline-secondary btn-sm" @click="getProduct(item.id)">
                            <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                            查看更多
                        </button>
                        <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addtoCart(item.id)">
                            <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                            加到購物車
                        </button>
                    </div>
                </div>
            </div>
        </div>

        <div class="cartList container" v-if="carts">
            <table class="table">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col">品名</th>
                        <th scope="col">數量</th>
                        <th scope="col">單價</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, key) in carts.carts" :key="key">
                        <td>
                            <button class="btn btn-outline-danger" @click="removeCartItem(item.id)">
                                <i class="fas fa-trash-alt"></i>
                            </button>
                        </td>
                        <td>
                            <span>{{item.product.title}}</span>
                            <div v-if="item.coupon" class="text-success">
                                已套用優惠券
                            </div>
                        </td>
                        <td>{{item.product.num}} / {{item.product.unit}}</td>
                        <td>{{item.product.price}}</td>
                    </tr>
                    <tr>
                        <td colspan="3" class="text-right">總計</td>
                        <td>{{carts.total}}</td>
                    </tr>
                    <tr v-if="carts.final_total != carts.total">
                        <td colspan="3" class="text-success text-right">折扣價</td>
                        <td class="text-success">{{carts.final_total}}</td>
                    </tr>
                </tbody>
            </table>
            <div class="input-group mb-3">
                <input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼">
                <div class="input-group-append">
                    <button class="btn btn-outline-secondary" type="button" @click="addCouponCode()">
                        套用優惠碼
                    </button>
                </div>
            </div>
        </div>
        <div class="my-5 row justify-content-center">
            <form class="col-md-6" @submit.prevent="createOrder()">
                <div class="form-group">
                    <label for="useremail">Email</label>
                    <input type="email" class="form-control" name="email" id="useremail"
                      :class="{'is-invalid':errors.has('email')}"  v-validate="'required|email'" v-model="form.user.email" placeholder="請輸入 Email">
                    <span class="text-danger" v-if="errors.has('email')">{{errors.first('email') }}</span>
                </div>
            
                <div class="form-group">
                    <label for="username">收件人姓名</label>
                    <input type="text" class="form-control" name="name" id="username"
                       :class="{'is-invalid':errors.has('name')}" v-validate="'required'" v-model="form.user.name" placeholder="輸入姓名">
                    <span class="text-danger" v-if="errors.has('name')">姓名必須輸入</span>
                </div>
            
                <div class="form-group">
                    <label for="usertel">收件人電話</label>
                    <input type="tel" class="form-control" name="tel" id="usertel" :class="{'is-invalid':errors.has('tel')}" v-validate="'required|numeric'" 
                        v-model="form.user.tel" placeholder="請輸入電話">
                    <span class="text-danger" v-if="errors.has('tel')">{{errors.first('tel') }}</span>
                </div>
            
                <div class="form-group">
                    <label for="useraddress">收件人地址</label>
                    <input type="text" class="form-control" name="address" id="useraddress"
                        :class="{'is-invalid':errors.has('address')}" v-validate="'required'" v-model="form.user.address" placeholder="請輸入地址">
                    <span class="text-danger" v-if="errors.has('address')">地址欄位不得留空</span>
                </div>
            
                <div class="form-group">
                    <label for="comment">留言</label>
                    <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
                </div>
                <div class="text-right">
                    <button class="btn btn-danger">送出訂單</button>
                </div>
            </form>
        </div>

        <!-- Product Detail Modal -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">
                            {{ product.title }}
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <img :src="product.imageUrl" alt="" class="img-fliud">

                        <blockquote class="blockquote mt-3">
                            <p class="mb-0">{{ product.contemt }}</p>
                            <footer class="blockquote-footer text-right">{{ product.description }}</footer>
                        </blockquote>

                        <div class="d-flex justify-content-between align-items-baseline">
                            <div class="h4" v-if="!product.price">{{product.origin_price}} 元</div>
                            <del class="h6" v-if="product.price">原價 {{product.origin_price}} 元</del>
                            <div class="h4" v-if="product.price">現在只要 {{product.price}} 元</div>
                        </div>

                        <select name="" id="" class="form-control mt-3" v-model="product.num">
                            <option :value="num" v-for="num in 10" :key="num">選購 {{num}} {{product.unit}}</option>
                        </select>
                    </div>
                    <div class="modal-footer">
                        <div class="text-muted text-nowrap mr-3">
                            小計 <strong> {{ product.num * product.price }} </strong> 元
                        </div>
                        <button type="button" class="btn btn-primary" @click="addtoCart(product.id, product.num)">
                            <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>
                            加到購物車
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';

export default {
    data(){
        return{
            products: [],
            product: {},
            carts: [],
            total: '',
            finalTotal: '',
            isLoading: false,
            status:{
                loadingItem: '',
            },
            coupon_code: '',
            form:{
                user: {
                    name: '',
                    email: '',
                    tel: '',
                    address: ''
                },
                message: ''
            }
        }
    },
    created(){
        this.getProducts();
        this.getCart();
    },
    methods: {
        getProducts(){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products/all`;
            vm.isLoading = true;
            this.$http.get(api).then((response) => {
                // console.log(response);
                vm.products = response.data.products;
                vm.isLoading = false;
            })
        },
        getProduct(id){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`;
            vm.status.loadingItem = id;
            this.$http.get(api).then((response) => {
                // console.log(response);
                $('#productModal').modal('show');
                vm.product = response.data.product;
                 vm.status.loadingItem = '';
                
            })
        },
        addtoCart(id, qty = 1){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            const cart = {
                product_id: id,
                qty
            }
            vm.status.loadingItem = id;
            this.$http.post(api, {data: cart}).then((response) => {
                console.log(response);
                vm.status.loadingItem = '';
                vm.getCart();
                $('#productModal').modal('hide');
            })
        },
        getCart(){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
            vm.isLoading = true;
            this.$http.get(api).then((response) => {
                console.log(response);
                vm.carts = response.data.data;
                vm.isLoading = false;
                
            })
        },
        removeCartItem(id){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
            vm.isLoading = true;
            this.$http.delete(api).then(() => {
                vm.getCart();
                vm.isLoading = false;   
            })
        },
        addCouponCode(){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
            const coupon = {
                code: vm.coupon_code
            }
            vm.isLoading = true;
            this.$http.post(api, {data: coupon}).then((response) => {
                console.log(response.data);
                vm.getCart();
                vm.isLoading = false;
            })
        },
        createOrder(){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
            const order = vm.form;
            // vm.isLoading = true;
            this.$validator.validate().then((valid) => {
                if (valid) {
                    this.$http.post(api, {data: order}).then((response) => {
                        console.log('訂單已建立', response);
                        if(response.data.success){
                            vm.$router.push(`/customer_checkout/${response.data.orderId}`)
                        }
                        vm.isLoading = false;
                        
                    })
                }else{
                    console.log('欄位不完整')
                }
            });
            
        }
    }
}
</script>

<style lang="scss" scoped>
.modal{
    img{
        width: 100%;
    }
}
</style>



